var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"writing"},[_c('h1',[_vm._v("Why did I pivot to Java?")]),_c('p',[_vm._v(" When I started my CS studies, it was all Java. It was used on programming introductory courses and thereafter very extensively during the undergraduate studies, with only one exception, the "),_c('i',[_vm._v("Programming languages and paradigms")]),_vm._v(" course, which introduced students to other ways of thinking about programming. I was by that point quite familiar with functional programming both as a concept and by playing around with Haskell, and had read about logic programming, both topics which were on the course. Around that time I also had my Lisp hype period. During data science master's degree studies programming language switched over to Python. So why on Earth would I after all that go back to Java? ")]),_c('p',[_vm._v(" Java has been lambasted as boring stupid huge unelegant corporate language and it's trendy to hate it. I admit, I fell into that thinking, thinking being \"there got to be a better way to make things than this one thing which for some reason is so widely used\". It's amazing really how thinking process ends there, not thinking things thorough properly, such as maybe all these thousands of companies and individuals actually have explored the alternatives for the past 25 years and decided on Java. Part Dunning-Kruger, part hubris. ")]),_c('p',[_vm._v(" Java is incredibly performant, and getting faster still with every release. This steady performance improvement release after release is noteworthy, as Java started its rise 25 years ago. The Java we are using today is much faster than the Java that ran on those antique computers, while computers are only getting ever faster. It kicks Python right out of ballfield. Not even PyPy saves Python's poor performance. Compared to C++, Java obviously lags behind, but only if the C++ developer really knows what he is doing. JVM optimizations are so good compiled Java code can be faster than C++, and the more development moves away from toy programs to larger programs, the more likely it is that performance leap over C++ will happen. ")]),_c('p',[_vm._v(" After performance considerations, the maintainability considerations must come. Since Java is object-oriented and strongly typed, software maintenance is relatively effortless compared to the alternatives, as I noted in my Lisp other text. I'm happy I took the detour of other undeniably more rapidly written languages to understand that. In the long run, the weakly typed hard to debug Lisp (or Python!) piece of code that took you 5 minutes to write compared to 15 minutes with Java will come and bite you in the ass. And in my view, software engineering should be long-term effort instead of constant rewrites of old codebase. Even JavaScript side of things have come to realize this, that a moment's struggle with getting the types right and extra effort with method signatures pays off. I am of course talking about the ever growing popularity of TypeScript. ")]),_c('p',[_vm._v(" Uses come next in priority because if language is nice and all but has no use and thus no ecosystem in industry why bother? And on servers Java is the king, and servers are where the cloud services are, and cloud services are where the majority of growth is now globally, even for Microsoft. Briefly coming back to the performance aspect of things regarding cloud services, big name companies such as Netflix and Twitter abandoned their old cloud platforms made with whatever in favour of Java-based infrastructure. Not only that, but Microsoft hosted their first ever Java conference in 2020 because of their reliance on Java. We'll come back to that in a bit. And of course Alibaba is running all of their systems on Java. ")]),_c('p',[_vm._v(" What about other uses than running a server? Java is better than before on desktop computing GUI front with JavaFX that has taken over Swing in popularity, at least on developer front when looking at GitHub metrics: as of writing JavaFX gets 32,507 repository results vs. Swing 26,134 repository results with Java language filter enabled. Things are looking especially bright now with JavaFX being decoupled off the main Java project, meaning it is developed independently. That will likely lead to even wider applications more rapidly than was possible with Swing. My pet project is made with JavaFX, and as a front side oriented developer I dig it, it follows the familiar separation of logic (Java), layout (XML) and style (CSS). Speaking of front side development, there is Vaadin for creating web UI's with Java. ")]),_c('p',[_vm._v(" Speaking of decoupling, when Java went modular after Java 8 in 2017 and the entire Java project did drastic changes in leadership, licensing and project ownership, I was cautious. Especially since the 6 month major release cycle seemed like madness after what was previously several years between major releases. But since then, the Java programming language continued to develop, and the changes brought beneficial changes to both developers and end-users in form of distribution, such as AdoptOpenJDK. It also has adopted a sensible cycle of LTS releases inbetween the rapid release cycle of 3 years between releases and over twice the years of support time so no constant life on razor required while providing something fresh to jump at frequently for those so inclined (I used to be one of those!). As Java is not so tied to Oracle anymore, it has more breathing room to develop to match uses of more users relying on it. ")]),_c('p',[_vm._v(" Lastly there is of course the tooling and language ecosystem. Coming from JavaScript world to Java and then discovering Maven was a revelation. Like NPM but for Java! I adopted it right away and so should you. Sure, there is Gradle for the JSON oriented people, but the good thing is no matter what you choose for project, the artifacts are the same. Like NPM and Yarn. Editors and IDEs are of course important, and there exists Java mode or plugins for every advanced editor I can think of. And then there are at least 3 good IDEs (Eclipse, IntelliJ, NetBeans) that come to mind, all of them multiplatform. ")]),_c('p',[_vm._v(" All of these considerations, and many more intertwined inbetween, made Java my choice for pivoting and specializing into. And should the worst happen for whatever black swan reason of Java becoming obsolete in my lifetime, its conventions inherited by those that came after it would surely make any transition to the new great a breeze. "),_c('i',[_vm._v("\"Java developer never goes hungry.\"")])]),_c('h2',[_vm._v("Appendix: Was C# ever an option?")]),_c('p',[_vm._v(" C# is intentionally similar to Java with roughly the same use cases. I gave serious thought into specializing into it instead of Java, but ultimately decided not to do it for several reasons. First of all, I came to understand that the performance under ASP.NET is dogshit compared to Java's Spring or JakartaEE. Obviously, if big names migrate to Java instead of ASP.NET there has to be something in big boy stories of cloud unviability of C# past certain point. Not to mention Microsoft basically admitting it themselves with that little Java conference of theirs. ")]),_c('p',[_vm._v(" Secondly, as an UI oriented man, there is something wrong about C# applications I get on Windows 10, either as a sides directly from Microsoft or from Microsoft Store. Relatively slow application loading times, and then there is no snappiness anywhere, and - now this is hard to explain in concrete measurable terms - but a "),_c('i',[_vm._v("visceral feeling of tardiness and gummy")]),_vm._v(" when using those. I am not sure if it's the animations or what, but I perceive a slight lag and delay on whatever I am doing with those applications. Despite things like Xamarin and how ubiquitous C# is on Windows, the thought of doing any UI work with C# seems just unprofessional. ")]),_c('p',[_vm._v(" Third is tooling. I came to understand there is nothing like Maven for C# so that's a big no. Second is IDEs, with Visual Studio Community being the only excellent free one that I know of. Problem being of course its ginormous size of about 10Gb just to get something done on Windows desktop. But I don't use only Windows so there is that too. Because of the almost total reliance on Visual Studio, C# quickly becomes a non-option. ")]),_c('p',[_vm._v(" All in all, C# was an option, but then again not really. Not from entreprenurial point of view, not from developer point of view. ")]),_c('footer',[_vm._v("2021-01-30")])])}]

